import { createRouter } from 'vue-router';
import Contacts from '../pages/Contacts.vue';
import Contact from '../pages/Contact.vue';

const routes = [
  {
    path: '/',
    redirect: '/contacts',
  },
  {
    path: '/contacts',
    name: 'contacts',
    component: Contacts,
  },
  {
    path: '/contacts/:contactId',
    name: 'contactDetail',
    component: Contact,
  },
];

const router = createRouter({
  mode: 'history',
  routes,
});

export default router;
